			<div class="info" :style='{"minHeight":"500px","width":"41%","padding":"0 0 20px","margin":"40px 7% 0 0","background":"none","order":"2"}'>
				<div class="item" :style='{"border":"0 dashed #7e6b5a","padding":"4px 20px 16px","margin":"0 0 10px 0","outline":"1px solid #7e6b5a","alignItems":"center","outlineOffset":"2px","background":"url(http://codegen.caihongy.cn/20230207/cb0df322e9b14cb3b11556207d9fab2b.png) no-repeat 20px -1px,#f6f5f3","borderWidth":"1px 0","display":"flex","justifyContent":"space-between"}'>
					<div :style='{"padding":"8px 20px 0 80px","fontSize":"16px","color":"#333","fontWeight":"500"}'>
                    {{detail.shangpinmingcheng}}
                    </div>
					<div @click="storeup(1)" v-show="!isStoreup" :style='{"padding":"12px 10px 2px","background":"none"}'><i v-if="true" :style='{"color":"#936837","fontSize":"16px"}' class="el-icon-star-off"></i><span :style='{"color":"#936837","fontSize":"16px"}'>点我收藏</span></div>
					<div @click="storeup(-1)" v-show="isStoreup" :style='{"padding":"12px 10px 2px","background":"none"}'><i v-if="true" :style='{"color":"#936837","fontSize":"16px"}' class="el-icon-star-on"></i><span :style='{"color":"#936837","fontSize":"16px"}'>取消收藏</span></div>
				</div>

				<div class="item" :style='{"border":"0 dashed #eee","padding":"0","margin":"0 20px 10px","background":"none","borderWidth":"0 0 1px","display":"flex","justifyContent":"spaceBetween"}' v-if="detail.price">
					<div class="lable" :style='{"padding":"0 10px","color":"#936837","textAlign":"right","width":"auto","fontSize":"14px","lineHeight":"40px","minWidth":"120px","height":"40px"}'>价格</div>
					<div style="font-weight: bold;" :style='{"width":"100%","padding":"0 10px","fontSize":"14px","lineHeight":"24px","color":"#666"}'><span :style='{"fontSize":"12px"}'>￥</span>{{detail.price}}</div>
				</div>
				<div class="item" :style='{"border":"0 dashed #eee","padding":"0","margin":"0 20px 10px","background":"none","borderWidth":"0 0 1px","display":"flex","justifyContent":"spaceBetween"}' v-if="detail.price">
					<div class="lable" :style='{"padding":"0 10px","color":"#936837","textAlign":"right","width":"auto","fontSize":"14px","lineHeight":"40px","minWidth":"120px","height":"40px"}'>会员价</div>
					<div style="font-weight: bold;" :style='{"width":"100%","padding":"0 10px","fontSize":"14px","lineHeight":"24px","color":"#666"}'><span :style='{"fontSize":"12px"}'>￥</span>{{detail.vipprice}}</div>
				</div>
				<div class="item" :style='{"border":"0 dashed #eee","padding":"0","margin":"0 20px 10px","background":"none","borderWidth":"0 0 1px","display":"flex","justifyContent":"spaceBetween"}' v-if="detail.jf">
					<div class="lable" :style='{"padding":"0 10px","color":"#936837","textAlign":"right","width":"auto","fontSize":"14px","lineHeight":"40px","minWidth":"120px","height":"40px"}'>积分</div>
					<div style="color: red;font-weight: bold;" :style='{"width":"100%","padding":"0 10px","fontSize":"14px","lineHeight":"24px","color":"#666"}'>{{detail.jf}}</div>
				</div>
				<div class="item" :style='{"border":"0 dashed #eee","padding":"0","margin":"0 20px 10px","background":"none","borderWidth":"0 0 1px","display":"flex","justifyContent":"spaceBetween"}'>
					<div class="lable" :style='{"padding":"0 10px","color":"#936837","textAlign":"right","width":"auto","fontSize":"14px","lineHeight":"40px","minWidth":"120px","height":"40px"}'>商品分类</div>
					<div  :style='{"width":"100%","padding":"0 10px","fontSize":"14px","lineHeight":"24px","color":"#666"}'>{{detail.shangpinfenlei}}</div>
				</div>
				<div class="item" :style='{"border":"0 dashed #eee","padding":"0","margin":"0 20px 10px","background":"none","borderWidth":"0 0 1px","display":"flex","justifyContent":"spaceBetween"}'>
					<div class="lable" :style='{"padding":"0 10px","color":"#936837","textAlign":"right","width":"auto","fontSize":"14px","lineHeight":"40px","minWidth":"120px","height":"40px"}'>规格</div>
					<div  :style='{"width":"100%","padding":"0 10px","fontSize":"14px","lineHeight":"24px","color":"#666"}'>{{detail.guige}}</div>
				</div>
				<div class="item" :style='{"border":"0 dashed #eee","padding":"0","margin":"0 20px 10px","background":"none","borderWidth":"0 0 1px","display":"flex","justifyContent":"spaceBetween"}'>
					<div class="lable" :style='{"padding":"0 10px","color":"#936837","textAlign":"right","width":"auto","fontSize":"14px","lineHeight":"40px","minWidth":"120px","height":"40px"}'>品牌</div>
					<div  :style='{"width":"100%","padding":"0 10px","fontSize":"14px","lineHeight":"24px","color":"#666"}'>{{detail.pinpai}}</div>
				</div>
				<div class="item" :style='{"border":"0 dashed #eee","padding":"0","margin":"0 20px 10px","background":"none","borderWidth":"0 0 1px","display":"flex","justifyContent":"spaceBetween"}'>
					<div class="lable" :style='{"padding":"0 10px","color":"#936837","textAlign":"right","width":"auto","fontSize":"14px","lineHeight":"40px","minWidth":"120px","height":"40px"}'>商家账号</div>
					<div  :style='{"width":"100%","padding":"0 10px","fontSize":"14px","lineHeight":"24px","color":"#666"}'>{{detail.shangjiazhanghao}}</div>
				</div>
				<div class="item" :style='{"border":"0 dashed #eee","padding":"0","margin":"0 20px 10px","background":"none","borderWidth":"0 0 1px","display":"flex","justifyContent":"spaceBetween"}'>
					<div class="lable" :style='{"padding":"0 10px","color":"#936837","textAlign":"right","width":"auto","fontSize":"14px","lineHeight":"40px","minWidth":"120px","height":"40px"}'>商家名称</div>
					<div  :style='{"width":"100%","padding":"0 10px","fontSize":"14px","lineHeight":"24px","color":"#666"}'>{{detail.shangjiamingcheng}}</div>
				</div>
				<div class="item" :style='{"border":"0 dashed #eee","padding":"0","margin":"0 20px 10px","background":"none","borderWidth":"0 0 1px","display":"flex","justifyContent":"spaceBetween"}'>
					<div class="lable" :style='{"padding":"0 10px","color":"#936837","textAlign":"right","width":"auto","fontSize":"14px","lineHeight":"40px","minWidth":"120px","height":"40px"}'>点击次数</div>
					<div  :style='{"width":"100%","padding":"0 10px","fontSize":"14px","lineHeight":"24px","color":"#666"}'>{{detail.clicknum}}</div>
				</div>
				<div class="btn" :style='{"padding":"10px 0","flexWrap":"wrap","display":"flex"}'>
					<el-input-number :style='{"width":"180px","margin":"0 5px 0 0","lineHeight":"38px","position":"relative","display":"inline-block"}'  :min=1 v-model="buynumber"></el-input-number>
					<el-button :style='{"border":"0","cursor":"pointer","padding":"0 10px","margin":"0 5px 0 0","outline":"none","color":"#333","borderRadius":"30px","background":"#e8d7c3","width":"auto","lineHeight":"40px","fontSize":"14px","height":"40px"}'  type="warning" size="small" @click="addCart">添加到购物车</el-button>
					<el-button :style='{"border":"0","cursor":"pointer","padding":"0 10px","margin":"0 5px 0 0","outline":"none","color":"#333","borderRadius":"30px","background":"#d9c0a7","width":"auto","lineHeight":"40px","fontSize":"14px","height":"40px"}'  type="warning" size="small" @click="buyNow">立即购买</el-button>
					<el-button :style='{"border":"0","cursor":"pointer","padding":"0 10px","margin":"0 5px 0 0","outline":"none","color":"#333","borderRadius":"30px","background":"#e4ccb8","width":"auto","lineHeight":"40px","fontSize":"14px","height":"40px"}'  type="warning" size="small" @click="jfTap">积分兑换</el-button>
				</div>
				<div class="btn" :style='{"padding":"10px 0","flexWrap":"wrap","display":"flex"}'>
					<el-button :style='{"border":"0","cursor":"pointer","padding":"0 10px","margin":"0 5px 0 0","outline":"none","color":"#333","borderRadius":"30px","background":"#d3eacc","width":"auto","lineHeight":"40px","fontSize":"14px","height":"40px"}' v-if="isAuth('shangpinxinxi','审核')" @click="onSH" type="warning">审核</el-button>
				</div>
			</div>
			
			<!-- 热门信息 -->
			<div class="hot" :style='{"width":"86%","margin":"40px 7% 0","position":"relative","background":"none","height":"auto","order":"4"}'>
			  <div :style='{"border":"0 dashed #7e6b5a","padding":"4px 0 6px 100px","outline":"1px solid #7e6b5a","color":"#4e4e4e","outlineOffset":"2px","textAlign":"left","background":"url(http://codegen.caihongy.cn/20230207/cb0df322e9b14cb3b11556207d9fab2b.png) no-repeat 20px -1px,#f6f5f3","borderWidth":"1px 0","width":"100%","lineHeight":"44px","fontSize":"18px"}'>热门信息</div>
			  <div :style='{"padding":"0","margin":"30px 0 0","flexWrap":"wrap","background":"none","display":"flex","width":"100%","justifyContent":"space-between","height":"auto"}'>
			    <div v-for="item in hotList" :key="item" :style='{"padding":"20px","margin":"0 0 10px","borderRadius":"8px","background":"url(http://codegen.caihongy.cn/20230207/346e413dce8f4515a5a57219df032ccd.png) no-repeat left top / auto 100%,url(http://codegen.caihongy.cn/20230207/e75d04594fa6401b9993f023e829f7b8.png) no-repeat right bottom / auto 100%","display":"flex","width":"23%","justifyContent":"center","height":"auto"}' @click="toDetail(item)">
			      <img :style='{"margin":"0 0 10px","objectFit":"cover","flex":"1","display":"block","width":"auto","height":"180px","maxWidth":"52%"}' :src="baseUrl + (item.tupian?item.tupian.split(',')[0]:'')" alt="">
			      <div :style='{"padding":"0 6px","margin":"0 10px 0 0","overflow":"hidden","color":"#333","alignItems":"center","textAlign":"center","display":"flex","width":"32px","lineHeight":"1.2","fontSize":"14px"}'>{{item.shangpinmingcheng}}</div>
			      <div :style='{"padding":"0 6px","margin":"0 10px 0 0","overflow":"hidden","color":"#333","alignItems":"center","textAlign":"center","display":"flex","width":"32px","lineHeight":"1.2","fontSize":"14px"}'>{{item.pinpai}}</div>
			      <div :style='{"padding":"0 6px","margin":"0 10px 0 0","overflow":"hidden","color":"#333","alignItems":"center","textAlign":"center","display":"flex","width":"32px","lineHeight":"1.2","fontSize":"14px"}'>{{item.shangjiamingcheng}}</div>
			      <!-- <div :style='{"padding":"0 5px","lineHeight":"12px","fontSize":"12px","color":"#999","textAlign":"right","display":"none"}'>2022-02-02</div> -->
			    </div>
			  </div>
			</div>
		</div>
		
		
		<el-tabs class="detail" :style='{"border":"0px solid #DCDFE6","minHeight":"440px","boxShadow":"0 0px 0px 0 rgba(0, 0, 0, .1)","margin":"60px 7% 0","overflow":"hidden","background":"#FFF","flex":"1","width":"50%","order":"10"}' v-model="activeName" type="border-card">
			<el-tab-pane label="商品详情" name="first">
				<div v-html="detail.shangpinxiangqing"></div>
			</el-tab-pane>
			<el-tab-pane label="评论" name="second">
				<el-form class="add comment" :style='{"width":"100%","boxShadow":"0 0px 0px 0 rgba(0, 0, 0, .1)","padding":"15px","margin":"0 0 20px"}' :model="form" :rules="rules" ref="form">
					<el-form-item class="item" :style='{"width":"100%","display":"flex","height":"auto"}' label="评论" prop="content">
						<el-input type="textarea" :rows="5" v-model="form.content" placeholder="请输入内容"></el-input>
					</el-form-item>
					<el-form-item class="btn" :style='{"width":"100%","padding":"0 0 0 80px","margin":"10px 0 0","height":"auto"}'>
						<el-button :style='{"border":"0 solid #cbbd9a","cursor":"pointer","padding":"0","margin":"0 20px 0 0","color":"#937937","outline":"none","borderRadius":"30px","background":"linear-gradient(30deg, rgba(255,255,255,1) 0%, rgba(207,193,176,1) 20%, rgba(246,236,223,1) 50%, rgba(207,193,176,1) 80%, rgba(255,255,255,1) 100%)","borderWidth":"1px 0","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}' type="primary" @click="submitForm('form')">立即提交</el-button>
						<el-button :style='{"border":"0 solid #ddd","cursor":"pointer","padding":"0","margin":"0 20px 0 0","color":"#888","outline":"none","borderRadius":"30px","background":"linear-gradient(30deg, rgba(255,255,255,1) 0%, rgba(200,200,200,1) 20%, rgba(240,240,240,1) 50%, rgba(200,200,200,1) 80%, rgba(255,255,255,1) 100%)","borderWidth":"1px 0","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}' @click="resetForm('form')">重置</el-button>
					</el-form-item>
				</el-form>
				
				<div v-if="infoList.length" :style='{"boxShadow":"0 0px 0px 0 rgba(0, 0, 0, .1)","padding":"15px","borderRadius":"12px","background":"#f6f5f3"}' class="comment">
					<div :style='{"padding":"10px","margin":"0 0 20px","borderColor":"#999","alignItems":"center","borderWidth":"0","width":"100%","borderStyle":"solid","height":"auto"}' v-for="item in infoList" :key="item.id">
						<div class="user" :style='{"width":"100%","alignItems":"center","display":"flex","height":"auto"}'>
							<el-image v-if="item.avatarurl" :style='{"width":"40px","margin":"0 10px 0 0","borderRadius":"100%","objectFit":"cover","height":"40px"}' :size="50" :src="baseUrl + item.avatarurl"></el-image>
							<el-image v-if="!item.avatarurl" :style='{"width":"40px","margin":"0 10px 0 0","borderRadius":"100%","objectFit":"cover","height":"40px"}' :size="50" :src="require('@/assets/touxiang.png')"></el-image>
							<div :style='{"color":"#333","fontSize":"16px"}' class="name">{{item.nickname}}</div>
						</div>
						<div :style='{"padding":"10px","boxShadow":"none","margin":"10px 0px 0px","color":"#333","borderRadius":"8px","background":"#fff","lineHeight":"24px","fontSize":"14px"}' class="content-block-ask">
							{{item.content}}
						</div>
						<div :style='{"padding":"10px","boxShadow":"none","margin":"10px 0px 0px","color":"#333","borderRadius":"8px","background":"#fff","lineHeight":"24px","fontSize":"14px"}' class="content-block-reply" v-if="item.reply">
							回复：{{item.reply}}
						</div>
					</div>
				</div>
				
				<el-pagination
				  background
				  class="pagination"
				  :pager-count="7"
				  :page-size="pageSize"
				  :page-sizes="pageSizes"
				  prev-text="<"
				  next-text=">"
				  :hide-on-single-page="true"
				  :layout='["total","prev","pager","next","sizes","jumper"].join()'
				  :total="total"
				  :style='{"padding":"0","margin":"10px auto","whiteSpace":"nowrap","color":"#333","textAlign":"center","width":"1200px","fontWeight":"500"}'
				  @current-change="curChange"
				  @prev-click="prevClick"
				  @next-click="nextClick"
				></el-pagination>
			</el-tab-pane>
			
		</el-tabs>
		<el-dialog title="审核" :visible.sync="sfshVisiable" width="50%">
			<el-form ref="shForm" :model="shForm" label-width="80px">
				<el-form-item label="审核状态">
					<el-select v-model="shForm.sfsh" placeholder="审核状态">
						<el-option label="通过" value="是"></el-option>
						<el-option label="不通过" value="否"></el-option>
						<el-option label="待审核" value="待审核"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="内容">
					<el-input type="textarea" :rows="8" v-model="shForm.shhf"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="sfshVisiable = false">取 消</el-button>
				<el-button type="primary" @click="shHandler">确 定</el-button>
			</div>
		</el-dialog>
	</div>
</div>
</template>

<script>
  import CountDown from '@/components/CountDown';
  export default {
    //数据集合
    data() {
      return {
        tablename: 'shangpinxinxi',
        baseUrl: '',
        breadcrumbItem: [
          {
            name: '详情信息'
          }
        ],
        title: '',
        detailBanner: [],
        endTime: 0,
        hotList: [],
        detail: {},
        activeName: 'first',
        form: {
          content: '',
          userid: localStorage.getItem('userid'),
          nickname: localStorage.getItem('username'),
          avatarurl: '',
        },
        infoList: [],
        total: 1,
        pageSize: 5,
		pageSizes: [10,20,30,50],
        totalPage: 1,
        rules: {
          content: [
            { required: true, message: '请输入内容', trigger: 'blur' }
          ]
        },
        storeupParams: {
          name: '',
          picture: '',
          refid: 0,
          tablename: 'shangpinxinxi',
          userid: localStorage.getItem('userid')
        },
        isStoreup: false,
        storeupInfo: {},
        buynumber: 1,
        cart: {
          buynumber: 0,
          discountprice: 0,
          goodid: 0,
          goodname: '',
          picture: '',
          price: 0,
          userid: localStorage.getItem('userid')
        },
        isInCart: false,
        sfshVisiable: false,
        shForm: {
          sfsh: '是',
          shhf: ''
        },
      }
    },
    created() {
        this.init();
    },
    //方法集合
    methods: {
        init() {
          this.baseUrl = this.$config.baseUrl;
          if(this.$route.query.detailObj) {
            this.detail = JSON.parse(this.$route.query.detailObj);
          }
          if(this.$route.query.storeupObj) {
            this.detail.id = JSON.parse(this.$route.query.storeupObj).refid;
          }
          if(this.$route.query.discussObj) {
            this.detail.id = JSON.parse(this.$route.query.discussObj).goodid;
          }
          this.$http.get(this.tablename + '/detail/'  + this.detail.id, {}).then(res => {
            if (res.data.code == 0) {
              this.detail = res.data.data;
              this.title = this.detail.shangpinmingcheng;
              this.detailBanner = this.detail.tupian ? this.detail.tupian.split(",") : [];
              this.$forceUpdate();
            }
          });

          this.getDiscussList(1);
          this.getStoreupStatus();
          this.getCartList();
          this.getHotList();

        },
        toDetail(item) {
          this.$router.push({path: '/index/shangpinxinxiDetail', query: {detailObj: JSON.stringify(item)}});
          this.init();
          document.body.scrollTop = 0
          document.documentElement.scrollTop = 0
        },
        getHotList() {
          let autoSortUrl = "";
          autoSortUrl = "shangpinxinxi/autoSort";
          if(localStorage.getItem('Token')) {
              autoSortUrl = "shangpinxinxi/autoSort2";
          }
            this.$http.get(autoSortUrl, {params: {
                sfsh: '是',
                page: 1,
                limit: 4,
            }}).then(res => {
                if (res.data.code == 0) {
                    this.hotList = res.data.data.list;        
                }
            })
        },
      onSH() {
        this.sfshVisiable = true;
      },
      shHandler(){
        this.$confirm(`确定操作?`, "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }).then(() => {
          this.detail.sfsh = this.shForm.sfsh;
          this.detail.shhf = this.shForm.shhf;
          this.$http.post(`${this.tablename}/update`, this.detail).then(res => {
            if (res.data.code == 0) {
              this.sfshVisiable = false;
              this.$message({
                message: "操作成功",
                type: "success",
                duration: 1500,
              });
            }
          });
        });
      },
      storeup(type) {
        if (type == 1 && !this.isStoreup) {
          this.storeupParams.name = this.title;
          this.storeupParams.picture = this.detailBanner[0];
          this.storeupParams.inteltype = this.detail.shangpinfenlei;
          this.storeupParams.refid = this.detail.id;
          this.storeupParams.type = type;
          this.$http.post('storeup/add', this.storeupParams).then(res => {
            if (res.data.code == 0) {
              this.isStoreup = true;
              this.$message({
                type: 'success',
                message: '收藏成功!',
                duration: 1500,
              });
            }
          });
        }
        if (type == -1 && this.isStoreup) {
          let delIds = new Array();
          delIds.push(this.storeupInfo.id);
          this.$http.post('storeup/delete', delIds).then(res => {
            if (res.data.code == 0) {
              this.isStoreup = false;
              this.$message({
                type: 'success',
                message: '取消成功!',
                duration: 1500,
              });
            }
          });
        }
      },
      getStoreupStatus(){
        if(localStorage.getItem("Token")) {
            this.$http.get('storeup/list', {params: {page: 1, limit: 1, type: 1, refid: this.detail.id, tablename: 'shangpinxinxi', userid: localStorage.getItem('userid')}}).then(res => {
              if (res.data.code == 0 && res.data.data.list.length > 0) {
                this.isStoreup = true;
                this.storeupInfo = res.data.data.list[0];
              }
            });
        }
      },
      curChange(page) {
        this.getDiscussList(page);
      },
      prevClick(page) {
        this.getDiscussList(page);
      },
      nextClick(page) {
        this.getDiscussList(page);
      },
      // 下载
      download(file){
        if(!file) {
            this.$message({
              type: 'error',
              message: '文件不存在',
              duration: 1500,
            });
            return;
        }
        window.open(this.baseUrl+file)
      },
      getDiscussList(page) {
        this.$http.get('discussshangpinxinxi/list', {params: {page, limit: this.pageSize, refid: this.detail.id}}).then(res => {
          if (res.data.code == 0) {
            this.infoList = res.data.data.list;
            this.total = res.data.data.total;
            this.pageSize = res.data.data.pageSize;this.pageSizes = [this.pageSize, this.pageSize*2, this.pageSize*3, this.pageSize*5];
            this.totalPage = res.data.data.totalPage;
          }
        });
      },
      submitForm(formName) {
        let sensitiveWords = "";
        let sensitiveWordsArr = [];
        if(sensitiveWords) {
            sensitiveWordsArr = sensitiveWords.split(",");
        }
        for(var i=0; i<sensitiveWordsArr.length; i++){
            //全局替换
            var reg = new RegExp(sensitiveWordsArr[i],"g");
            //判断内容中是否包括敏感词
            if (this.form.content.indexOf(sensitiveWordsArr[i]) > -1) {
                // 将敏感词替换为 **
                this.form.content = this.form.content.replace(reg,"**");
            }
        }
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$http.get('orders/list', {params: {page: 1, limit: 1, status: '已完成', goodid: this.detail.id, userid: localStorage.getItem('userid')}}).then(res => {
                if (res.data.code == 0 && res.data.data.list.length == 0) {
                  this.$message({
                    type: 'success',
                    message: '请完成订单后再评论!',
                    duration: 1500
                  });
                  return false
                } else {
                    this.form.refid = this.detail.id;
                    this.form.avatarurl = localStorage.getItem('headportrait')?localStorage.getItem('headportrait'):'';
                    this.$http.post('discussshangpinxinxi/add', this.form).then(res => {
                      if (res.data.code == 0) {
                        this.form.content = '';
                        this.getDiscussList(1);
                        this.$message({
                          type: 'success',
                          message: '评论成功!',
                          duration: 1500,
                        });
                      }
                    });
                }
            });
          } else {
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      getCartList() {
        this.$http.get('cart/list', {params: {userid: localStorage.getItem('userid'), tablename: 'shangpinxinxi', goodid: this.detail.id}}).then(res => {
          if (res.data.code == 0) {
            if (res.data.data.list.length > 0) {
              this.isInCart = true;
            } else {
              this.isInCart = false;
            }
          }
        });
      },
      addCart() {
        if (this.isInCart) {
          this.$message.error('该商品已经添加到购物车');
          return;
        }

        this.cart.buynumber = this.buynumber;
        this.cart.goodid = this.detail.id;
        this.cart.goodname = this.title;
        this.cart.tablename = this.tablename;
        this.cart.shangjiazhanghao = this.detail.shangjiazhanghao;
        this.cart.goodtype = this.detail.shangpinfenlei;
        this.cart.picture = this.detailBanner[0];
        this.cart.price = (localStorage.getItem('vip')=='是'&&this.detail.vipprice>0)?this.detail.vipprice:this.detail.price;
        this.$http.post('cart/save', this.cart).then(res => {
          if (res.data.code === 0) {
            this.getCartList();
            this.$message({
              message: '添加成功',
              type: 'success',
              duration: 1500,
            });
          } else {
            this.$message.error(res.data.msg);
          }
        });
      },
        //立即购买
        buyNow() {
            // 保存到storage中，在确认下单页面中获取要购买的商品
            localStorage.setItem('orderGoods', JSON.stringify([{
                tablename: this.tablename,
                goodid: this.detail.id,
                goodname: this.title,
                shangjiazhanghao: this.detail.shangjiazhanghao,
                goodtype: this.detail.shangpinfenlei,

                picture:this.detailBanner[0],
                buynumber: this.buynumber,
                userid: localStorage.getItem('userid'),
                price: (localStorage.getItem('vip')=='是'&&this.detail.vipprice>0)?this.detail.vipprice:this.detail.price,
                discountprice: this.detail.vipprice ? this.detail.vipprice : 0
            }]));
            // 跳转到确认下单页面
            let query = {
                type: 1,
            }
            this.$router.push({path: '/index/shop-order/orderConfirm', query: query});
        },


        // 积分兑换
        jfTap() {
            // 活动倒计时限制
            // 保存到storage中，在确认下单页面中获取要购买的商品
            localStorage.setItem('orderGoods', JSON.stringify([{
                tablename: this.tablename,
                goodid: this.detail.id,
                goodname: this.title,
                shangjiazhanghao: this.detail.shangjiazhanghao,
                goodtype: this.detail.shangpinfenlei,

                picture:this.detailBanner[0],
                buynumber: this.buynumber,
                userid: localStorage.getItem('userid'),
                price: this.detail.jf,
                discountprice: this.detail.jf ? this.detail.jf : 0
            }]));
            // 跳转到确认下单页面,并且携带参数type=2代表是积分兑换
            let query = {
              type: 2,
            }
            this.$router.push({path: '/index/shop-order/orderConfirm', query: query});
        },
    },
    components: {
      CountDown
    }
  }
</script>

